/** Hide element while making it readable for screen readers Shamelessly borrowed from HTML5Boilerplate: https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133 */
.visually-hidden { border: 0; clip: rect(0 0 0 0); height: 1px; margin: -1px; overflow: hidden; padding: 0; position: absolute; width: 1px; }

header { padding: 1rem 0; overflow: hidden; position: relative; }

header h1 { margin-top: 0; margin-bottom: 0; font-weight: 300; font-size: 2.5rem; }

header .site-title img { background: #222222; }

header .menu { padding: 0; margin: 0; display: inline-block; }

header .menu .menu-item, header .menu .social-item { list-style: none; display: inline-block; }

header .menu .menu-item a { font-weight: 300; padding: 1rem 1rem; margin: 0; transition: .25s; color: #222222; font-family: "Fjalla One", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; text-transform: uppercase; letter-spacing: 1px; }

header .menu .menu-item a:hover, header .menu .menu-item a:active, header .menu .menu-item a:focus { background: #222222; color: #fff; transform: scale(1.07); }

header .menu .social-item a { font-size: 1.65rem; padding: 0 .5rem 0; color: #e1e1e1; transition: .3s; }

header .menu .social-item a:hover, header .menu .social-item a:active, header .menu .social-item a:focus { color: #222222; transform: scale(1.1); }

header.header-pages { padding: 1.75rem 0; }

header.header-pages .site-title img { height: 48px; }

header.header-pages .site-title .menu { margin-left: 1rem; }

header.header-pages nav ul { float: right; text-align: right; }

@media screen and (max-width: 63.9375em) { header.header-pages nav ul { float: left; text-align: left; margin-top: 1rem; }
  header.header-pages nav .menu .menu-item { display: block; } }

.screen-reader-text { display: none; }

footer { text-align: center; font-weight: 400; font-size: .65rem; margin: 1rem auto 0; }

footer .footer-info { margin-bottom: .5rem; }

footer .footer-info:last-child { margin-bottom: 1rem; }

footer .footer-info span { margin-right: 1rem; }

footer .footer-info span:first-child { font-weight: 700; text-transform: uppercase; }

footer .footer-info span:last-child { margin-right: 0; }

footer .contact-info { font-size: .75rem; }

footer a { color: #666; }

.edit-bar { text-align: center; font-weight: 700; }

button, input[type='button'], input[type='submit'] { background-color: #333; color: #fff; border: none; font-weight: 300; padding: 1rem 1rem; margin: 0; transition: all 0.3s ease 0s; font-family: "Fjalla One", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; text-transform: uppercase; letter-spacing: 1px; }

button:hover, button:focus, input[type='button']:hover, input[type='button']:focus, input[type='submit']:hover, input[type='submit']:focus { background-color: #fff; color: #fff; }

button:hover, button:active, button:focus, input[type='button']:hover, input[type='button']:active, input[type='button']:focus, input[type='submit']:hover, input[type='submit']:active, input[type='submit']:focus { background: #fff; color: #222222; transform: scale(1.07); }

/* Preload images */
body:after { content: url("./dist/imgs/lightbox//close.png") url("./dist/imgs/lightbox//loading.gif") url("./dist/imgs/lightbox//images/prev.png") url("./dist/imgs/lightbox//images/next.png"); display: none; }

body.lb-disable-scrolling { overflow: hidden; }

.lightboxOverlay { position: absolute; top: 0; left: 0; z-index: 9999; background-color: black; filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80); opacity: 0.8; display: none; }

.lightbox { position: absolute; left: 0; width: 100%; z-index: 10000; text-align: center; line-height: 0; font-weight: normal; }

.lightbox .lb-image { display: block; height: auto; max-width: inherit; max-height: none; border-radius: 3px; /* Image border */ border: 4px solid white; }

.lightbox a img { border: none; }

.lb-outerContainer { position: relative; *zoom: 1; width: 250px; height: 250px; margin: 0 auto; border-radius: 4px; /* Background color behind image. This is visible during transitions. */ background-color: white; }

.lb-outerContainer:after { content: ""; display: table; clear: both; }

.lb-loader { position: absolute; top: 43%; left: 0; height: 25%; width: 100%; text-align: center; line-height: 0; }

.lb-cancel { display: block; width: 32px; height: 32px; margin: 0 auto; background: url("./dist/imgs/lightbox/loading.gif") no-repeat; }

.lb-nav { position: absolute; top: 0; left: 0; height: 100%; width: 100%; z-index: 10; }

.lb-container > .nav { left: 0; }

.lb-nav a { outline: none; background-image: url("data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="); }

.lb-prev, .lb-next { height: 100%; cursor: pointer; display: block; }

.lb-nav a.lb-prev { width: 34%; left: 0; float: left; background: url("./dist/imgs/lightbox/prev.png") left 48% no-repeat; filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0); opacity: 0; -webkit-transition: opacity 0.6s; -moz-transition: opacity 0.6s; -o-transition: opacity 0.6s; transition: opacity 0.6s; }

.lb-nav a.lb-prev:hover { filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100); opacity: 1; }

.lb-nav a.lb-next { width: 64%; right: 0; float: right; background: url("./dist/imgs/lightbox/next.png") right 48% no-repeat; filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0); opacity: 0; -webkit-transition: opacity 0.6s; -moz-transition: opacity 0.6s; -o-transition: opacity 0.6s; transition: opacity 0.6s; }

.lb-nav a.lb-next:hover { filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100); opacity: 1; }

.lb-dataContainer { margin: 0 auto; padding-top: 5px; *zoom: 1; width: 100%; -moz-border-radius-bottomleft: 4px; -webkit-border-bottom-left-radius: 4px; border-bottom-left-radius: 4px; -moz-border-radius-bottomright: 4px; -webkit-border-bottom-right-radius: 4px; border-bottom-right-radius: 4px; }

.lb-dataContainer:after { content: ""; display: table; clear: both; }

.lb-data { padding: 0 4px; color: #ccc; }

.lb-data .lb-details { width: 85%; float: left; text-align: left; line-height: 1.1em; }

.lb-data .lb-caption { font-size: 13px; font-weight: bold; line-height: 1em; }

.lb-data .lb-caption a { color: #4ae; }

.lb-data .lb-number { display: block; clear: left; padding-bottom: 1em; font-size: 12px; color: #999999; }

.lb-data .lb-close { display: block; float: right; width: 30px; height: 30px; background: url("./dist/imgs/lightbox/close.png") top right no-repeat; text-align: right; outline: none; filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70); opacity: 0.7; -webkit-transition: opacity 0.2s; -moz-transition: opacity 0.2s; -o-transition: opacity 0.2s; transition: opacity 0.2s; }

.lb-data .lb-close:hover { cursor: pointer; filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100); opacity: 1; }

.banner { margin-bottom: 2rem; height: 70vh; background: rgba(255, 255, 255, 0.85); position: relative; }

.banner h1, .banner h2 { position: absolute; bottom: 1rem; left: 1rem; font-size: 3rem; text-transform: uppercase; font-family: "Fjalla One", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; }

.banner h1 a, .banner h2 a { display: block; font-size: 1rem; font-family: "Lato", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; font-weight: 400; }

.banner .banner-background { position: absolute; bottom: 0; height: 70vh; width: 100%; background-image: url("./dist/imgs/header.jpg"); background-repeat: none; background-attachment: fixed; background-position: center center; background-size: cover; z-index: -1; display: block; }

main.site-main { padding: 0; }

main.site-main #freewall { padding-bottom: 3px; }

main.site-main .brick { position: relative; }

main.site-main .brick img { width: 100%; }

main.site-main .brick .paint { transition: .2s; position: absolute; bottom: 0; font-weight: 700; font-size: 1.5rem; color: #fff; background: rgba(255, 255, 255, 0.8); height: 100%; width: 100%; vertical-align: middle; padding: .5rem; opacity: 0; }

main.site-main .brick .paint title { transition: .3s; position: absolute; bottom: -1rem; left: 1rem; color: #222222; display: block; letter-spacing: 1px; font-family: "Fjalla One", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; text-transform: uppercase; }

main.site-main .brick a:hover .paint, main.site-main .brick a:active .paint, main.site-main .brick a:focus .paint { opacity: 1; }

main.site-main .brick a:hover .paint title, main.site-main .brick a:active .paint title, main.site-main .brick a:focus .paint title { bottom: 1rem; }

header.header-landing { height: 95vh; background: rgba(255, 255, 255, 0.9); margin-bottom: 3px; }

header.header-landing .header-background { position: absolute; bottom: 0; height: 95vh; width: 100vw; background-repeat: none; background-attachment: fixed; background-position: center center; background-size: cover; z-index: -1; }

header.header-landing .site-title { text-align: center; margin: 0 auto; }

header.header-landing .site-title img { margin-top: 10vh; height: 50vh; margin-bottom: 1rem; }

header.header-landing nav { text-align: center; margin: 0 auto; margin-top: 1.5rem; }

header.header-landing nav .menu li a { font-size: 3vh; }

main.project-main .content { margin-top: 2rem; }

main.project-main .thumbs-item img { opacity: 1; transition: .3s; overflow: hidden; }

main.project-main .thumbs-item img:hover, main.project-main .thumbs-item img:active, main.project-main .thumbs-item img:focus { opacity: .5; transform: scale(0.95); }

main.project-main .video { position: relative; height: 0; padding-bottom: 56.25%; margin-bottom: 2rem; }

main.project-main .video iframe { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.page > .banner { height: 40vh; }

.page > .banner .banner-background { height: 40vh; }
